import React, { memo, useState } from "react";
import { Answer, AnswerContent, IconQuestion, Items, Question, TextQuestion, Wrapper } from "./styled";

const dataFAQ = [
    {
        question: 'Believe you can exchange the world for the better with Bitcoin?',
        answer: 'Believe you can exchange the world for the better with Bitcoin, Blockchain, and Binance? Join the Binance Affiliate Program, and get rewarded for',
        active: false
    },
    {
        question: 'Believe you can exchange the world for the better with Bitcoin?',
        answer: 'Believe you can exchange the world for the better with Bitcoin, Blockchain, and Binance? Join the Binance Affiliate Program, and get rewarded for',
        active: false
    },
    {
        question: 'Believe you can exchange the world for the better with Bitcoin?',
        answer: 'Believe you can exchange the world for the better with Bitcoin, Blockchain, and Binance? Join the Binance Affiliate Program, and get rewarded for',
        active: false
    }
]

const FAQ: React.FC = () => {

    const [data, setData] = useState(dataFAQ)

    return (
        <>
            <Wrapper>
                {
                    data?.map((item, index) => (
                        <Items key={index}>
                            <Question onClick={() => {
                                let __item = data
                                __item[index].active = !__item[index].active;
                                setData([...__item])
                            }}>
                                <IconQuestion className={item?.active ? 'active' : ''} />
                                <TextQuestion>{item?.question}</TextQuestion>
                            </Question>
                            <Answer className={item?.active ? 'active' : ''}>
                                <AnswerContent>{item?.answer}</AnswerContent>
                            </Answer>
                        </Items>
                    ))
                }
            </Wrapper>
        </>
    )
}

export default memo(FAQ)