import React, { memo, useState } from "react";
import { View, WrapForm } from "./styled";
import { Checkbox, Form, Input, InputNumber } from 'antd';
import AuthenNavigation from 'components/Navigration/authen'
import styles from '../styles.module.css'
import { NavLink } from "react-router-dom";

const Login = () => {
    const [formCreate] = Form.useForm();
    const [formVerify] = Form.useForm();
    const [step, setStep] = useState(0);

    const onFinishCreate = async () => {
        const fileds = formCreate.getFieldsValue()
        console.log('==============>', fileds);
        setStep(1)
    };

    const onFinishVerify = async () => {
        const fileds = formVerify.getFieldsValue()
        console.log('==============>', fileds);
        setStep(0)
    };

    return (
        <>
            <View>
                <AuthenNavigation />
                <WrapForm>
                    {
                        step === 0
                            ? (
                                <Form
                                    name="basic"
                                    form={formCreate}
                                    className={`${styles.smarttrix_form}`}
                                    onFinish={onFinishCreate}
                                    autoComplete="off"
                                >
                                    <p className={styles.smarttrix_titleForm}>Create Your Account</p>
                                    <p className={styles.smarttrix_descriptionForm}>Welcome! Please enter your details.</p>
                                    <Form.Item
                                        label="Email"
                                        name="email"
                                        rules={[{ required: true, message: 'Please input your email!' }]}
                                    >
                                        <Input type="email" placeholder="example @mail.com" />
                                    </Form.Item>

                                    <Form.Item
                                        label="Password"
                                        name="password"
                                        rules={[{ required: true, message: 'Please input your password!' }]}
                                    >
                                        <Input.Password placeholder="min 8 characters" />
                                    </Form.Item>


                                    <Form.Item
                                        label="Referral code*"
                                        name="referralCode"
                                        rules={[{ required: true, message: 'Please input your referral code!' }]}
                                    >
                                        <Input />
                                    </Form.Item>

                                    <Form.Item className="remember-me">
                                        <Form.Item className="_remember-me" name="remember" valuePropName="checked">
                                            <Checkbox>
                                                <span
                                                    style={{
                                                        whiteSpace: 'pre-wrap',
                                                        fontWeight: '500',
                                                        fontSize: '14px',
                                                        lineHeight: '17px',
                                                        color: 'rgba(255,255,255,0.8)'
                                                    }}
                                                >
                                                    I have read and agree to Smart Matrix's <NavLink
                                                        style={{
                                                            whiteSpace: 'pre-wrap',
                                                            fontWeight: '500',
                                                            fontSize: '14px',
                                                            lineHeight: '17px',
                                                            color: 'rgba(255,255,255,0.8)',
                                                            textDecoration: 'underline'
                                                        }}
                                                        to="/">Terms of Service and Privacy Policy.</NavLink>
                                                </span>
                                            </Checkbox>
                                        </Form.Item>
                                    </Form.Item>

                                    <Form.Item>
                                        <button className={styles.smarttrix_buttonSubmit} type="submit">
                                            Create Account
                                        </button>
                                    </Form.Item>
                                    <Form.Item style={{ marginBottom: 0 }}>
                                        <div className={styles.smarttrix_moreOption}>
                                            <p>Already have an account? &nbsp;</p>
                                            <NavLink className={styles.smarttrix_moreOption_link} to="/login">Login</NavLink>
                                        </div>
                                    </Form.Item>
                                </Form>
                            ) : (
                                <Form
                                    name="basic"
                                    form={formVerify}
                                    className={`${styles.smarttrix_form}`}
                                    onFinish={onFinishVerify}
                                    autoComplete="off"
                                >
                                    <p className={styles.smarttrix_titleForm}>Email Verification</p>
                                    <p className={styles.smarttrix_descriptionForm}>Please enter the 6-digit verification code that was sent to your email. The code is valid for 120s.</p>
                                    <Form.Item>
                                        <Form.Item
                                            label="Email Verification Code"
                                            name="code"
                                            rules={[{ required: true, message: 'Please input code!' }]}
                                            style={{ marginBottom: 0 }}
                                        >
                                            <InputNumber placeholder="6- digit" />
                                        </Form.Item>
                                        <p className={styles.smarttrix_resendCode}>Resend code</p>
                                    </Form.Item>

                                    <Form.Item>
                                        <button className={styles.smarttrix_buttonSubmit} type="submit">
                                            Submit
                                        </button>
                                    </Form.Item>
                                    <Form.Item style={{ marginBottom: 0 }}>
                                        <div className={styles.smarttrix_moreOption}>
                                            <p>Didn't receive the code? &nbsp;</p>
                                        </div>
                                    </Form.Item>
                                </Form>
                            )
                    }
                </WrapForm>
            </View >
        </>
    )
}

export default memo(Login)