import React from "react";
import { Route, Switch, Redirect } from "react-router-dom";
import { AppRoute } from "config/router/config.router";
import { Layout } from 'antd';

import { Wrapper } from './styled';

import Login from "views/Authen/Login";
import Register from "views/Authen/Register";
import Overview from 'views/Overview'

import { useHookSider } from "state/sider";
import Sider from "components/Sider";
import Header from "components/Header";
import Mission from "views/Mission";
import Comunity from "views/Comunity";
import Transaction from "views/Transaction";
import Setting from "views/Setting";

const { Content } = Layout;

const Routers = () => {

  const [state] = useHookSider();

  return (
    <>
      <Switch>
        <Route exact path={AppRoute.login} component={Login} />
        <Route exact path={AppRoute.register} component={Register} />
        <Route>
          <Wrapper collapse={state?.collapse}>
            <Layout>
              {/* SIDEBAR LEFT */}
              <Sider />

              <Layout className="site-layout">
                {/* HEADER */}
                <Header />

                <Content className='content'>
                  <Switch>
                    <Route exact path={AppRoute.overview} component={Overview} />
                    <Route exact path={AppRoute.mission} component={Mission} />
                    <Route exact path={AppRoute.comunity} component={Comunity} />
                    <Route exact path={AppRoute.transaction} component={Transaction} />
                    <Route exact path={AppRoute.setting} component={Setting} />
                    <Route exact path={AppRoute.all}>
                      <Redirect to={AppRoute.overview} />
                    </Route>
                  </Switch>
                </Content>
              </Layout>
            </Layout>
          </Wrapper>
        </Route>
      </Switch>
    </>
  );
};

export default Routers;
